@import url("https://fonts.googleapis.com/css2?family=Spartan:wght@500;700&display=swap");

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Spartan", sans-serif;
}

:root {
  --primaryCyan: hsl(180, 29%, 50%);
  --backgroundCyan: hsl(180, 52%, 96%);
  --filterCyan: hsl(180, 31%, 95%);
  --darkCyan: hsl(180, 8%, 52%);
  --veryDarkCyan: hsl(180, 14%, 20%);
}

body {
  width: 100vw;
  min-height: 100vh;
  overflow-x: hidden;
  background-color: var(--backgroundCyan);
}

header {
  width: 100%;
  min-height: 15vh;
  background-image: url("../public/images/bg-header-mobile.svg");
  background-position: bottom;
  background-size: cover;
  background-color: var(--primaryCyan);
  position: relative;
}

.filter {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: -2.5rem;
  width: 90%;
  max-width: 1000px;
  min-height: 10vh;
  background-color: #fff;
  padding: 1.5rem;
  border-radius: 0.25rem;
  box-shadow: 5px 5px 0.75rem -0.25rem hsl(180deg 14% 20% / 30%);
  display: flex;
  align-items: center;
}

.filter-tags {
  width: 95%;
  display: flex;
  gap: 0.5rem;
  align-items: center;
  flex-wrap: wrap;
}

.filter-tag-el {
  font-size: 0.8rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 0.45rem;
  font-weight: 700;
  background-color: var(--filterCyan);
  color: var(--primaryCyan);
  border-radius: 0.25rem;
}

.filter-tag-el div {
  padding: 0.45rem;
  border-radius: 0 0.25rem 0.25rem 0;
}

.close-btn {
  height: 100%;
  background-color: var(--primaryCyan);
  color: #fff;
  cursor: pointer;
  user-select: none;
  transition: all 250ms ease-in-out;
}
.close-btn:hover {
  background-color: var(--veryDarkCyan);
}

.clear {
  font-size: 0.95rem;
  cursor: pointer;
  margin-left: auto;
  font-weight: 700;
  color: var(--darkCyan);
}
.clear:hover {
  color: var(--primaryCyan);
  text-decoration: underline;
}

.card {
  width: 90%;
  margin: 3rem auto;
  background-color: #fff;
  padding: 1.5rem;
  border-radius: 0.25rem;
  position: relative;
  box-shadow: 5px 5px 0.75rem -0.25rem hsl(180deg 14% 20% / 30%);
}

.card:first-of-type {
  margin-top: 5rem;
}

.company-logo {
  position: absolute;
  width: 3.5rem;
  height: 3.5rem;
  left: 1rem;
  top: -1rem;
}

.top {
  width: 100%;
  display: flex;
  align-items: center;
  margin-top: 2rem;
  margin-bottom: 0.5rem;
  flex-wrap: wrap;
}

.company-info {
  margin-top: 1.25rem;
  padding-bottom: 1rem;
  border-bottom: 0.07rem solid var(--darkCyan);
}

.company-name {
  font-weight: 700;
  color: var(--primaryCyan);
  margin-right: 1rem;
}

.new,
.featured {
  font-size: 0.55rem;
  text-transform: uppercase;
  padding: 0.45rem;
  color: #fff;
  margin: 0 0.25rem;
  border-radius: 0.75rem;
  font-weight: 700;
}

.new {
  background-color: var(--primaryCyan);
}

.featured {
  background-color: var(--veryDarkCyan);
}

.role {
  font-weight: 700;
  color: var(--veryDarkCyan);
  font-size: 0.9rem;
  margin: 0.85rem 0;
  cursor: pointer;
  transition: all 250ms ease-in-out;
}
.role:hover {
  color: var(--primaryCyan);
}

.extra-info {
  font-size: 0.75rem;
}

.extra-info > span {
  margin-right: 0.45rem;
  color: var(--darkCyan);
}

.tags {
  width: 85%;
  padding: 0.5rem 0;
  padding-top: 1.5rem;
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
}

.tag {
  padding: 0.5rem;
  background-color: var(--filterCyan);
  color: var(--primaryCyan);
  font-weight: 700;
  font-size: 0.85rem;
  border-radius: 0.35rem;
  cursor: pointer;
  transition: all 250ms ease-in-out;
  user-select: none;
}
.tag:hover {
  color: #fff;
  background-color: var(--primaryCyan);
}
.tag:active {
  color: #fff;
  background-color: var(--primaryCyan);
  transform: scale(0.85) !important;
}

@media only screen and (min-width: 700px) {
  header {
    background-image: url("../public/images/bg-header-desktop.svg");
  }
}

footer {
  width: 100%;
  min-height: 8vh;
  display: grid;
  place-items: center;
}

footer,
footer a {
  color: var(--primaryCyan);
  font-size: 0.8rem;
}

footer a {
  color: var(--veryDarkCyan) !important;
}

@media only screen and (min-width: 1000px) {
  header {
    min-height: 20vh;
  }
  .card {
    position: unset;
    display: flex;
    align-items: center;
    max-width: 1000px;
    padding-block: 2.5rem;
  }
  .company-info,
  .tags {
    width: 50%;
  }
  .company-logo {
    position: unset;
    display: inline-block;
    width: 5rem;
    height: 5rem;
  }
  .company-info,
  .top {
    margin-top: unset;
  }

  .company-info {
    border-bottom: unset;
    padding-bottom: unset;
    padding-left: 1.5rem;
  }

  .tags {
    display: flex;
    justify-content: flex-end;
  }

  .tag {
    font-size: 0.75rem;
  }
}
